.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

@media screen and (min-width: 36rem) {
  .container {
    max-width: 31.875rem;
  }
}

@media screen and (min-width: 48rem) {
  .container {
    max-width: 43.125rem;
  }
}

@media screen and (min-width: 62rem) {
  .container {
    max-width: 58.125rem;
  }
}

@media screen and (min-width: 75rem) {
  .container {
    max-width: 69.375rem;
  }
}

@media screen and (min-width: 20rem) {
  .container {
    padding-left: 0.9375rem;
    padding-right: 0.9375rem;
  }
}

@media screen and (min-width: 36rem) {
  .container {
    padding-left: 1.875rem;
    padding-right: 1.875rem;
  }
}

#section__headline {
  align-self: center;
  box-sizing: content-box;
  display: inline-block;
  vertical-align: top;
  text-align: center;
}

.icon__awards {
  display: inline-block;
  width: 38px;
  height: 41px;
  background: url("../img/aboutUs/icon-award.png");
}

.icon__jobs {
  display: inline-block;
  width: 41px;
  height: 40px;
  background: url("../img/aboutUs/icon-checked.png");
}

.icon__happy {
  display: inline-block;
  width: 33px;
  height: 39px;
  background: url("../img/aboutUs/icon-dima.png");
}

.icon__reviews {
  display: inline-block;
  width: 41px;
  height: 37px;
  background: url("../img/aboutUs/icon-briefcase.png");
}

.section-header {
  display: block;
  margin: 0 auto;
  text-align: center;
  font-family: "Amsdam";
  font-size: 1.25em;
  font-weight: 400;
  text-transform: uppercase;
  white-space: nowrap;
}

.section-header__headline {
  display: inline-block;
  padding: 30px 25px 25px 25px;
  margin-bottom: 20px;
  color: #3e3d3d;
  border-bottom: 3px solid transparent;
  border-left: none;
  border-right: none;
  border-image: url(../img/other/divider-dark.png) 150 round;
}

@media screen and (min-width: 36rem) {
  .section-header__headline {
    padding: 60px 25px 25px 25px;
    margin-bottom: 40px;
  }
}

.section-header__headline--light {
  padding-top: 70px;
  color: #ffffff;
  border-image: url(../img/other/divider.png) 150 round;
}

.section-header__title {
  margin-top: 38px;
  margin-bottom: 58px;
  font-family: "CrimsonTextRoman";
  color: #3e3d3d;
}

.section-header__title--light {
  color: #ffffff;
}

.social {
  display: flex;
  justify-content: space-between;
  width: 60px;
  height: 12px;
}

.social-item__link--divider::before {
  content: "|";
}

.social-item__link--divider::after {
  content: "|";
}

.main-header {
  color: #3e3d3d;
  background-image: url("../img/slider/Image.png");
  background-repeat: no-repeat;
  background-size: cover;
