.slider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}

.slider__item {
  max-width: 12.5rem;
  padding: 15px 15px 140px;
  font-size: 0.625em;
}

@media screen and (min-width: 36rem) {
  .slider__item {
    max-width: 18.75rem;
    font-size: 0.75em;
  }
}

@media screen and (min-width: 48rem) {
  .slider__item {
    max-width: 31.25rem;
    font-size: 1em;
  }
}

.slider__sup-headline {
  font-family: "CrimsonTextRoman";
  font-size: 1.125em;
  font-weight: 400;
}

.slider__headline {
  margin-top: 25px;
  padding: 30px 15px;
  line-height: 1.5;
  text-transform: uppercase;
  font-family: "Amsdam";
  font-size: 2em;
  font-weight: 400;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: none;
  border-right: none;
  border-image: url(../img/other/divider.png) 150 round;
}

.slider__link {
  position: relative;
  bottom: -40px;
  margin: 0 auto;
  padding: 15px 30px;
  border: 2px solid #ffffff;
  color: #ffffff;
  text-transform: capitalize;
  white-space: nowrap;
  font-family: "Monserrat";
  font-size: 1.25em;
  font-weight: 400;
}

.slider__link::before {
  content: url(../img/slider/Arrow.png);
  position: absolute;
  left: 50%;
  bottom: -3rem;
  transform: translateX(-50%);
}

.slider button.slick-arrow {
  position: absolute;
  z-index: 999;
  top: calc(25% + 1vw);
  display: block;
  width: 35px;
  height: 35px;
  background: #ffffff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  font-family: "Amsdam";
  font-size: 0;
  font-weight: 400;
}

.slider button.slick-arrow.slick-prev {
  left: 0;
}

.slider button.slick-arrow.slick-prev::after {
  content: "<";
  line-height: 0.5;
  font-size: 20px;
}

.slider button.slick-arrow.slick-next {
  right: 0;
}

.slider button.slick-arrow.slick-next::after {
  content: ">";
  line-height: 0.5;
  font-size: 20px;
}
